<script>
import axios from 'axios'
export default {
  data() {
    return {
      emailSubject: '',
      emailText: ''
    }
  },
  methods: {
    sendEmail() {
      let email = {
        subject: this.emailSubject,
        text: this.emailText
      }
      this.$store.dispatch('sendEmail', email)
      console.log(email);
      $('#mailModal').modal('hide')
      this.emailSubject = ''
      this.emailText = ''
    }
  }
}
